.gallery__image {
  height: 80%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  display: block;
  text-decoration: none;
  height: 100%;
  width: 100%;
}

.gallery__link:hover {
  cursor: zoom-in;
  transform: scale(1.03);

}